* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: Lato, sans-serif;
    --background: #2c2f33;
    --darker-background: #23272a;
    --even-darker-background: #1a1d20;
    --text-color: white;
    --link-color: cornflowerblue;
    background: var(--background);
    color: var(--text-color);
}

[data-tab-content] {
    display: none;
}

.active[data-tab-content] {
    display: block;
}

.tabs {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    background: var(--darker-background);
}

.tab {
    cursor: pointer;
    padding: 10px;
    display: flex;
    align-items: center;
    transition: 250ms;
    & > .material-icons {
        margin-left: 5px;
    }
    & > .material-icons:hover {
        color: red;
        transition: 150ms;
    }
    &.active {
        background-color: var(--background);
    }
    &:hover {
        background-color: var(--even-darker-background);
    }
}

.material-icons {
    font-size: inherit !important;
}

.container {
    margin: 40px;
}

.block {
    display: block;
}

.link-btn {
    background: none;
    color: var(--link-color);
    outline: none;
    border: none;
    cursor: pointer;
    font-family: inherit;
    font-size: inherit;
    padding: 5px 0;
}

.browser-view {
    width: 100%;
    height: 100%;
}

.cm-editor.cm-focused {
    outline: none !important;
}
